import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import '../style/custom.css'
import config from '../../data/SiteConfig'
import IntlProvider from '../util/intlProvider'
import MainLayout from '../util/mainLayout'
import NavBar from '../components/Navbar'
import ArenaMicrosoftProgram from '../components/ArenaMicrosoftProgram'
import ArenaHackathon from '../components/ArenaHackathon'
import PartnersArenaConnect from '../components/PartnersArenaConnect'
import FooterArenaPage from '../components/FooterArenaPage/Footer'
 
const Section = ({ children, className }) => <div className={className}>{children}</div>
 
const Main = ({ langKey, messages }) => (
  <IntlProvider langKey={langKey} messages={messages}>
    <div>
      <NavBar langKey={langKey} color="Sara" />
      <MainLayout langKey={langKey} theme="mainarena" footerTheme="footer">
        <Section>
          <ArenaMicrosoftProgram />
        </Section>
        <Section>
          <ArenaHackathon />
        </Section>
        <Section>
          <PartnersArenaConnect />
        </Section>
        <FooterArenaPage config={config} langKey={langKey} />
      </MainLayout>
    </div>
  </IntlProvider>
)
 
export default Main


